  border-top-left-radius: 255px 15px;
  border-top-right-radius: 15px 225px;
  transition: opacity 235ms ease-in-out 0s;
  background: rgba(0,0,0,.7);
  border: 2px solid #41403e;
  color: #fff;
  font-size: .7em;
  left: 50%;
  min-width: 80px;
  opacity: 0;
  padding: 4px 2px;
  position: absolute;
  text-align: center;
  top: -6px;
  -webkit-transform: translateX(-50%) translateY(-100%);
  transform: translateX(-50%) translateY(-100%)
}
[popover-left]::before {
  left: 0;
  margin-left: -12px;
  top: 50%;
  -webkit-transform: translateY(-50%) rotate(-90deg);
  transform: translateY(-50%) rotate(-90deg)
}
[popover-left]::after {
  content: attr(popover-left);
  left: 0;
  margin-left: -8px;
  top: 50%;
  -webkit-transform: translateX(-100%) translateY(-50%);
  transform: translateX(-100%) translateY(-50%)
}
[popover-right]::before {
  left: 100%;
  margin-left: 1px;
  top: 50%;
  -webkit-transform: translatey(-50%) rotate(90deg);
  transform: translatey(-50%) rotate(90deg)
}
[popover-right]::after {
  content: attr(popover-right);
  left: 100%;
  margin-left: 8px;
  top: 50%;
  -webkit-transform: translateX(0) translateY(-50%);
  transform: translateX(0) translateY(-50%)
}
[popover-top]::before {
  left: 50%
}
[popover-top]::after {
  content: attr(popover-top);
  left: 50%
}
[popover-bottom]::before {
  margin-top: 8px;
  top: 100%;
  -webkit-transform: translateX(-50%) translatey(-100%) rotate(-180deg);
  transform: translateX(-50%) translatey(-100%) rotate(-180deg)
}
[popover-bottom]::after {
  content: attr(popover-bottom);
  margin-top: 8px;
  top: 100%;
  -webkit-transform: translateX(-50%) translateY(0);
  transform: translateX(-50%) translateY(0)
}
