  display: flex;
  padding: .3rem;
  position: relative;
  width: 100%;
  z-index: 100
}
@media only screen and (max-width:768px) {
  nav {
    display: block
  }
}
nav .bar1,
nav .bar2,
nav .bar3 {
  background-color: #41403e;
  border-bottom: 5px solid #41403e;
  border-bottom-left-radius: 15px 5px;
  border-bottom-right-radius: 15px 3px;
  color: #41403e;
  margin: 6px 0;
  transition: .4s;
  width: 2rem
}
nav .collapsible input[id^=collapsible]:checked + button .bar1 {
  -webkit-transform: rotate(-45deg) translate(-9px,7px);
  transform: rotate(-45deg) translate(-9px,7px)
}
nav .collapsible input[id^=collapsible]:checked + button .bar2 {
  opacity: 0
}
nav .collapsible input[id^=collapsible]:checked + button .bar3 {
  -webkit-transform: rotate(45deg) translate(-8px,-9px);
  transform: rotate(45deg) translate(-8px,-9px)
}
nav.split-nav {
  justify-content: space-between
}
nav.fixed {
  left: 0;
  position: fixed;
  right: 0;
  top: 0
}
nav div {
  margin: 0 1rem
}
nav ul.inline {
  margin-bottom: 0;
  margin-top: 10px;
  padding: 0
}
nav ul.inline li {
  display: inline-block;
  margin: 0 .5rem
}
@media only screen and (max-width:768px) {
  nav ul.inline li {
    display: block;
    margin: 1rem 0
  }
}
nav a {
  background-image: none;
  border-bottom: 5px solid #41403e;
  border-bottom-left-radius: 15px 3px;
  border-bottom-right-radius: 15px 5px;
  color: #41403e;
  padding-bottom: .1rem
}
nav a:hover {
  border-bottom: 5px solid #c1c0bd
}
nav ul.inline li a {
  font-size: 1.3rem
}
nav ul.inline li::before {
  content: ''
}
@media only screen and (max-width:992px) {
  nav ul {
    text-align: center
  }
}
nav .nav-brand h1,
nav .nav-brand h2,
nav .nav-brand h3,
nav .nav-brand h4,
nav .nav-brand h5,
nav .nav-brand h6 {
  margin: 0;
  margin-bottom: .2rem
}
nav .collapsible input[id^=collapsible]:checked ~ div.collapsible-body {
  margin: 0;
  max-height: 960px;
  opacity: 1;
  padding: 0
}
nav .collapsible .collapsible-body,
nav .collapsible:nth-of-type(1) {
  border: 0
}
@media only screen and (min-width:769px) {
  nav .collapsible .collapsible-body,
  nav .collapsible:nth-of-type(1) {
    display: contents
  }
}
nav div.collapsible-body {
  padding: none
}
nav .collapsible label {
  border-bottom: 0;
